
                    @use "sass:math";
                    @use "src/variables" as *;
                    @use "src/mixins" as *;
                  
@use 'variables' as *;
@use 'mixins' as *;

.app {
  height: 100vh;
  
  .sections {
    width: 100vw;
    height: calc(100vh);
    background-color: $first;
    position: relative;
    top: 0;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;
    
    &::-webkit-scrollbar {
      display: none;
    }

    > * {
      width: 100vw;
      height: calc(100vh);
      scroll-snap-align: start;
    }
  }
}
