
                    @use "sass:math";
                    @use "src/variables" as *;
                    @use "src/mixins" as *;
                  
@use '../../variables' as *;
@use '../../mixins' as *;

.nav-container {
  position: absolute;
  top: 2.5rem;
  left: 2.2rem;
  z-index: 2;
  background-color: transparent;
  width: 117px;
  height: 100px;
  overflow: hidden;
  @media only screen and (min-width: 160.3rem) {
    width: 234px;
    height: 200px;
  }
  @media only screen and (max-width: 160.2rem) and (min-width: 120.3rem) {
    width: 175.5px;
    height: 150px;
  }
  @media only screen and (max-width: 20rem) {
    // tab-land
    left: 1rem;
  }
  @media only screen and (max-height: 33.65rem) and (orientation: landscape) {
    top: 1rem;
  }
  .nav-iconfalse {
    background-color: #000;
    border-style: none;
    position: absolute;
    right: 3rem;
    top: 3rem;
    z-index: 10;
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    cursor: pointer;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @media only screen and (min-width: 160.3rem) {
      width: 200px;
      height: 200px;
    }
    @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
      width: 9.5rem;
      height: 9.5rem;
    }
    @media only screen and (max-width: 20rem) {
      // tab-land
      right: 2rem;
    }
    @media only screen and (max-height: 17.5rem) and (orientation: landscape) {
      top: 1rem;
    }
    @media only screen and (max-height: 33.65rem) and (orientation: landscape) {
      top: 1.5rem;
    }
    .bar1,
    .bar2,
    .bar3 {
      width: 20px;
      height: 2px;
      margin: 4px 0;
      transition: 0.4s;
      background-color: $tmain;
      border-radius: 1rem;
      @media only screen and (min-width: 160.3rem) {
        width: 50px;
        height: 5px;
      }
      @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
        width: 30px;
        height: 4px;
      }
    }
    .bar2 {
      width: 30px;
      transform-origin: right;
      @media only screen and (min-width: 160.3rem) {
        width: 70px;
        height: 5px;
      }
      @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
        width: 50px;
      }
    }
  }
  .nav-icontrue {
    background-color: #000;
    border-style: none;
    position: absolute;
    right: 3rem;
    top: 3rem;
    z-index: 10;
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    cursor: pointer;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @media only screen and (min-width: 160.3rem) {
      width: 200px;
      height: 200px;
    }
    @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
      width: 9.5rem;
      height: 9.5rem;
    }
    @media only screen and (max-width: 20rem) {
      // tab-land
      right: 2rem;
    }
    @media only screen and (max-height: 17.5rem) and (orientation: landscape) {
      top: 1rem;
    }
    @media only screen and (max-height: 33.65rem) and (orientation: landscape) {
      top: 1.5rem;
    }
    .bar1,
    .bar2,
    .bar3 {
      width: 20px;
      height: 2px;
      border-radius: 1em;
      margin: -1px;
      transition: 0.4s;
      background-color: $tmain;
      @media only screen and (min-width: 160.3rem) {
        margin: -2.5px;
        width: 50px;
        height: 5px;
      }
      @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
        margin: -1.5px;
        width: 30px;
        height: 4px;
      }
    }
    .bar2 {
      width: 30px;
      transform-origin: right;
      @media only screen and (min-width: 160.3rem) {
        width: 70px;
        height: 5px;
      }
      @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
        width: 50px;
      }
    }
    .bar1 {
      transform: rotate(-45deg);
    }
    .bar2 {
      opacity: 0;
    }
    .bar3 {
      transform: rotate(45deg);
    }
  }
  .nav-main {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    display: none;
    .box-wrapper {
      overflow: hidden;
      top: 0;
      left: 0;
      z-index: 1;
      display: flex;
      .box {
        background-color: $second;
        width: calc(100vw / 4);
        height: 100vh;
      }
    }
    .nav-left {
      position: fixed;
      opacity: 1;
      z-index: 2;
      color: $first;
      padding: 1em;
      left: auto;
      font-size: 1rem;
      top: 25%;
      left:5%;
      width: calc(100vw / 3);
      @media only screen and (max-width: 46em) {
        display: none;
      }
      @media only screen and (max-width: 80em) {
        right: 55%;
      }
      @media only screen and (max-height: 35em) {
        display: none;
      }
      .socials {
        font-size: 1.5em;
        @media only screen and (min-width: 160.3rem) {
          font-size: 5.5em;
        }
        @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
          font-size: 3.5em;
        }
      }
      .socialbox {
        display: inline-flex;
        flex-direction: column;
        width: 100%;
        margin-bottom: calc(20vh);
        
        .socialrow{
          display: inline-flex;
          flex-direction: row;
          margin-top:1rem;
          margin-bottom: 2rem;
          .social{
            align-items: center;
            flex: 2;
            
            .showHover{
              position: fixed;
              padding-left: 26px;
              padding-top:0px;
              padding-right: 10px;
              font-size:1.5em;
              
              color:#000;
              opacity: 0;
              transition: opacity 0.5s;
              @media only screen and (min-width: 160.3rem) {
              padding-left: 105px;
              padding-top:20px;
              flex-grow: 1;
              height: 6rem;
              font-size:3.5em;
              }
              @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
              padding-left: 50px;
              padding-top:7px;
              flex-grow: 1;
              height: 3rem;
              font-size:2em;
              }
            }
          .socialContent{
            height: 1.7rem;
            margin-right: 2rem;
            overflow: hidden;
            color:#000;  
            img{
              width: 25px;
              height: 24px;
              @media only screen and (min-width: 160.3rem) {
              width: 100px;
              height: 96px;
              }
              @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
                width: 50px;
              height: 48px;
              }
            }
          }
          
        }
        :hover .showHover{
          opacity: 1;

        }
        
        
          
        }
        
      }
      .p2 {
        overflow: hidden;
        margin: 0.5rem 0.5rem 0rem 0.5rem;
        font-size: 2.9rem;
        letter-spacing: 0px;
        transition: letter-spacing 0.5s;
        text-decoration: none;
        color: $first;
        a {
          text-decoration: none;
          color: $first;
        }
        @media only screen and (max-height: 52rem ) and (min-width: 120.3rem) {
          display:none;
        }
        @media only screen and (max-height: 77rem ) and (min-width: 160.3rem) {
          display:none;
        }
        @media only screen and (min-width: 160.3rem) {
          font-size:6.9rem;
        }
        @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
          font-size:4.9rem ;
        }
      }
    }
    .nav-right {
      position: fixed;
      opacity: 1;
      z-index: 2;
      color: $first;
      left: 60%;
      top: 30%;
      font-size: 2.5rem;
      height: calc(100vh - 30%);
      overflow: hidden;
      @media only screen and (max-width: 46em) {
        left: 10%;
        width: 100vw;
      }

      @media only screen and (max-height: 35em) {
        left: auto;
        flex-direction: column;
        flex-wrap: wrap;
        margin: 10px;
      }
      @media only screen and (min-width: 160.3rem) {
        font-size:6.5rem;
      }
      @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
        font-size:4.5rem ;
      }
      .nav-flex {
        height: calc(100vh - 30% - 70px);
        width: 70vw;
        font-size: 2.5rem;
        display: flex;
        flex-direction: column;
        @media only screen and (max-width: 46em) {
          left: auto;
          width: 100vw;
        }

        @media only screen and (max-height: 35em) {
          left: auto;
          flex-direction: row;
          flex-wrap: wrap;
        }
      }
      .p1 {
        color: $first;
        font-family: "Courier New", Courier, monospace;
        font-weight: bold;
        font-size: inherit;
        overflow: hidden;
        transition: 0.5s;
      }
      .p2 {
        overflow: hidden;
        margin: 0.5rem 0.5rem 0rem 0.5rem;
        font-size: 2.9rem;
        letter-spacing: 0px;
        transition: letter-spacing 0.5s;
        text-decoration: none;
        color: $first;
        a {
          text-decoration: none;
          color: $first;
        }
        &:hover {
          letter-spacing: 10px;
        }
        @media only screen and (min-width: 160.3rem) {
          font-size:6.9rem;
        }
        @media only screen and (max-width: 160rem) and (min-width: 120.3rem) {
          font-size:4.9rem ;
        }
      }
     
    }
  }

}
